/* Keyframes for the fade-in */

@keyframes fadeIn { 
    0% { 
        opacity:0; 
    } 
    
    100% {
        opacity: 1;
    } 
}

@keyframes fadeOut { 
    0% { 
        opacity:0; 
    } 
    
    100% {
        opacity: 1;
    } 
}




@keyframes moveInLeft {
  0% {
      opacity: 0;
      transform: translateX(-10rem);
  }

  80% {
      transform: translateX(1rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
      opacity: 0;
      transform: translateX(10rem);
  }

  80% {
      transform: translateX(-1rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}

@keyframes moveInBottom {
  0% {
      opacity: 0;
      transform: translateY(3rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}